}

body {
  font-family: "Lexend Exa", serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #171717;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a, ul, li {
  text-decoration: none;
  list-style: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lexend Exa", serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

nav, ul, li, a, p {
  padding: 0;
  margin: 0;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  background-color: #000000;
  height: 100vh;
  padding: 1rem;
  box-shadow: 0 0 0.5rem 0 #000000;
  width: max-content;
}
.header nav ul li {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.header nav ul li a {
  color: #FFFFFF;
  display: flex;
  align-items: center;
  gap: 0.8rem;
  font-size: 1.5rem;
}
.header nav ul li a:hover {
  transition: 0.1s ease transform;
  transform: scale(1.1);
}
.header nav ul li a:hover:last-child {
  transform: scale(1);
}

.principal {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 10fr;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10rem;
}
.logo img {
  max-width: 10rem;
  height: auto;
  animation: girar 1s ease;
}
.logo p {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
}
.logo span {
  color: #aefaff;
}

.log {
  margin-top: 15rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.log-btn {
  box-shadow: 0 0 0.2rem 0 #FFFFFF;
  background-color: #000000;
  border-radius: 2rem;
  justify-content: center;
  font-weight: bold;
  padding: 0 1rem;
}

@keyframes girar {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.main {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: scroll;
  scrollbar-width: 1rem;
  scrollbar-color: #202020 transparent;
  padding: 1rem;
  gap: 2rem;
}
.main h1 {
  color: #FFFFFF;
  text-align: left;
}

.index-nav {
  display: flex;
  gap: 5rem;
}
.index-nav a {
  color: #FFFFFF;
}

.carrusel-img {
  display: flex;
  overflow-x: scroll;
  gap: 1rem;
}
.carrusel-img img {
  width: auto;
  height: 25rem;
}

.nav_superior {
  display: flex;
  margin: 2rem;
}
.nav_superior ul {
  display: flex;
  width: 100%;
  position: relative;
}
.nav_superior ul li {
  display: flex;
  gap: 5rem;
}
.nav_superior ul li a {
  color: #FFFFFF;
}
.nav_superior ul li:last-of-type {
  margin-right: auto;
  position: absolute;
  right: 0;
}

.img-scale:hover {
  transition: 0.2s ease scale;
  scale: 0.95;
  cursor: pointer;
}

.navegacion-barra {
  box-shadow: 0 0 0.8rem 0 #000000;
  border-radius: 2rem;
  padding: 0.8rem;
}
.navegacion-barra a:hover {
  transition: 0.1s ease transform;
  transform: scale(1.1);
}

.forms {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #FFFFFF;
  background-color: #000000;
  border-radius: 2rem;
  width: auto;
  max-width: min-content;
  max-height: 50rem;
  padding: 2rem;
  box-shadow: 0 0 0.5rem 0 rgb(0, 0, 0);
}
.forms p {
  color: #969696;
  font-size: 1rem;
}
.forms fieldset {
  border: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.forms fieldset legend {
  margin-bottom: 2rem;
  text-align: center;
}
.forms fieldset legend::after {
  content: "";
  display: block;
  background-color: #FFFFFF;
  width: 100%;
  height: 0.1rem;
}
.forms fieldset input {
  background-color: transparent;
  border: none;
  border-radius: 1rem;
  box-shadow: 0 0 0.2rem 0 #969696;
  padding: 1rem;
  color: #FFFFFF;
  width: auto;
}
.forms fieldset .add_img {
  width: 25rem;
}

.forms-btn {
  display: block;
  width: max-content;
  margin-top: 2rem;
  padding: 1rem 2rem;
  border-radius: 2rem;
}

.main-crear {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4rem;
}

.error {
  padding: 0.5rem;
  background-color: #ff8080;
  border: solid 0.2rem #ff0000;
  border-radius: 2rem;
  color: black;
  animation: error 0.5s ease-in-out;
  display: flex;
  font-size: 1rem;
}

@keyframes error {
  0% {
    opacity: 0;
    transform: translateY(-1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.correcto {
  padding: 1rem;
  background-color: #e1feb0;
  border: solid 0.2rem #71B100;
  border-radius: 2rem;
  color: black;
  animation: correcto 0.5s ease-in-out;
}
.correcto p {
  color: black;
}

@keyframes correcto {
  0% {
    opacity: 0;
    transform: translateY(-1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.quitarAlerta {
  animation: desvanecer 0.5s ease-out;
}

@keyframes desvanecer {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    transform: translateY(-1rem);
    opacity: 0;
  }
}
.add-img {
  color: #FFFFFF;
  padding: 3rem;
}

.contenedor-albums {
  margin-top: 3rem;
  display: flex;
  flex-wrap: wrap; /* Acomoda los álbumes en varias filas si es necesario */
  justify-content: center; /* Centra los álbumes dentro del contenedor */
}

.album {
  position: relative;
  background-color: #000000;
  color: #FFFFFF;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  padding: 1rem;
  width: 25rem;
  height: 15rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.album h3 {
  font-size: 2rem;
  font-weight: bold;
}
.album a {
  color: #aefaff;
  font-size: 1.6rem;
}
.album::before {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 10rem;
  height: 2.5rem;
  left: 0;
  top: -5%;
  border-top-right-radius: 2rem;
}
.album .btn-eliminar {
  position: absolute;
  top: 0;
  left: 0;
  margin: 1rem;
}
.album .btn-eliminar input {
  color: #ff0000;
  border-radius: 2rem;
  background-color: transparent;
  border: none;
  font-size: 1.2rem;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}
.gallery .imagen {
  display: flex;
  flex-direction: column;
  width: min-content;
}
.gallery .imagen img {
  width: auto;
  height: 25rem;
}
.gallery .imagen .imagen_txt {
  color: #FFFFFF;
  background-color: #000000;
  padding: 0.5rem;
}

.login-forms {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-left {
  background-color: #000000;
  box-shadow: 0 0 0.8rem 0 #000000;
}

.login-right {
  display: flex;
  flex-direction: column;
  position: relative;
}

.login-left, .login-right {
  flex: 1; /* Cada uno tomará la mitad del espacio */
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login-logo img {
  width: 20rem;
  animation: girar 1s ease;
}
.login-logo p {
  color: #FFFFFF;
  text-align: center;
  font-size: 3rem;
}
.login-logo span {
  color: #aefaff;
}

@keyframes girar {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.error_container {
  position: absolute;
  top: 1rem;
  height: 1rem;
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}